import { Card, Row, Col, Typography, Tabs, Button, Table } from "antd";
import Link from "next/link";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import {
  CDetailAssetData,
  CDetailAssetImage,
  CDetailAttachments,
  CPageTitle,
  CTitle,
  ErrorNotification,
} from "@components";
import CApi from "@api";

const { Text } = Typography;
const { TabPane } = Tabs;
const AssetInformation = () => {
  const [noInduk, setNoInduk] = useState("");
  const [tagNumber, setTagNumber] = useState("");
  const [noBarang, setNoBarang] = useState("");
  const [oldNumber, setOldNumber] = useState("");
  const [name, setName] = useState("");
  const [branch, setBranch] = useState("");
  const [assetCategory, setAssetCategory] = useState("");
  const [status, setStatus] = useState("");
  const [flag, setFlag] = useState("");
  const [location, setLocation] = useState("");
  const [statusActiva, setStatusActiva] = useState("");
  const [vendor, setVendor] = useState("");
  const [qrCodeUrl, setQrCodeUrl] = useState("");
  const [value, setValue] = useState("");
  const [anggaran, setAnggaran] = useState("");
  const [masaManfaat, setMasaManfaat] = useState("");
  const [uselife, setUselife] = useState("");
  const [remark, setRemark] = useState("");
  const [model, setModel] = useState("");
  const [serialNumber, setSerialNumber] = useState("");
  const [partNumber, setPartNumber] = useState("");
  const [year, setYear] = useState("");
  const [codeActiva, setCodeActiva] = useState("");
  const [ownership, setOwnership] = useState("");
  const [assetType, setAssetType] = useState("");
  const [isImageSatu, setIsImageSatu] = useState(false);
  const [isImageDua, setIsImageDua] = useState(false);
  const [imageUrlSatu, setImageUrlSatu] = useState("");
  const [imageUrlDua, setImageUrlDua] = useState("");
  const [attachments, setAttachments] = useState();
  const [loadingAttachments, setLoadingAttachments] = useState(false);
  const [loadingDisposal, setLoadingDisposal] = useState(false);
  const [disposal, setDisposal] = useState([]);
  const [merk, setMerk] = useState("");
  const [tglPerolehan, setTglPerolehan] = useState("");
  const [currencyPerolehan, setCurrencyPerolehan] = useState("");
  const [hargaPerolehan, setHargaPerolehan] = useState("");
  const [paymentVoucher, setPaymentVoucher] = useState("");
  const [disposalDescription, setDisposalDecription] = useState("");
  const [isDisposal, setIsDisposal] = useState(false);
  const [dataAudit, setDataAudit] = useState([]);
  const [loadingHistory, setLoadingHistory] = useState(false);
  const [forms, setForms] = useState({
    induk_barang: "",
    description: "",
    jumlah: "",
    code_activa_id: "",
    asset_type_id: "",
  });

  useEffect(() => {
    getData();
  }, []);

  const router = useRouter();
  let id = router.query.id;

  const getData = async () => {
    await CApi.get("api/asset/" + id)
      .then((r) => {
        setNoInduk(r.data.no_induk_barang);
        setOldNumber(r.data.old_number);
        setTagNumber(r.data.tag_number);
        setName(r.data.name);
        setNoBarang(r.data.no_barang ? r.data.no_barang : "");
        setBranch(r.data.branch ? r.data.branch.name : "");
        setAssetCategory(
          r.data.asset_category ? r.data.asset_category.name : ""
        );
        setLocation(r.data.location ? r.data.location.name : "");
        setFlag(r.data.flag ? r.data.flag : "");
        setStatus(r.data.status ? r.data.status.name : "");
        setVendor(r.data.vendor ? r.data.vendor.name : "");
        setAssetType(r.data.asset_type ? r.data.asset_type.name : "");
        setCodeActiva(r.data.code_activa ? r.data.code_activa.name : "");
        setOwnership(r.data.ownership ? r.data.ownership.name : "");
        setQrCodeUrl(r.data.qr_code_url);
        setStatusActiva(r.data.status_activa ? r.data.status_activa : "");
        setValue(r.data.value ? r.data.value : "");
        setAnggaran(r.data.anggaran ? r.data.anggaran.name : "");
        setMasaManfaat(r.data.masa_manfaat ? r.data.masa_manfaat : "");
        setUselife(r.data.uselife ? r.data.uselife : "");
        setRemark(r.data.remark ? r.data.remark : "");
        setModel(r.data.model ? r.data.model : "");
        setSerialNumber(r.data.serial_number ? r.data.serial_number : "");
        setPartNumber(r.data.part_number ? r.data.part_number : "");
        setYear(r.data.year ? r.data.year : "");
        setMerk(r.data.merek_barang ? r.data.merek_barang : "");
        setTglPerolehan(r.data.tgl_perolehan ? r.data.tgl_perolehan : "");
        setCurrencyPerolehan(
          r.data.currency_perolehan ? r.data.currency_perolehan : ""
        );
        setHargaPerolehan(r.data.currency ? r.data.currency : "");
        setPaymentVoucher(r.data.payment_voucher ? r.data.payment_voucher : "");
        setForms({
          ...forms,
          induk_barang: r.data.no_barang,
        });

        getImage(r.data.id);
        getFileAttachments(r.data.id);
        getHistory(r.data.id);
      })
      .catch((err) => {
        if ("statusText" in err.response && "message" in err.response.data) {
          ErrorNotification(err.response.statusText, err.response.data.message);
        }
        router.push("/asset");
      });
  };

  const getHistory = async () => {
    setLoadingHistory(true);
    await CApi.get("api/asset/history/" + id)
      .then((r) => {
        setDataAudit(r.data);
      })
      .finally(() => {
        setLoadingHistory(false);
      });
  };

  const getImage = async (id) => {
    await CApi.get("api/images/" + id).then((res) => {
      const img = res.data;
      img.map((items) => {
        if (items.isOrigin === true) {
          setImageUrlSatu(items.ext + items.file);
          setIsImageSatu(true);
        } else {
          setImageUrlDua(items.ext + items.file);
          setIsImageDua(true);
        }
      });
    });
  };
  const getFileAttachments = async (id) => {
    setLoadingAttachments(true);
    await CApi.get("api/file-attachments/" + id).then((res) => {
      const data = res.data;
      setAttachments(data);
      setLoadingAttachments(false);
    });
  };

  const columnsDisposal = [
    {
      title: "File Name",
      width: 450,
      key: "name_file",
      render: (item) =>
        item.mime == "application/pdf" ? (
          <Link href={"file-attachments/" + JSON.stringify(item.id)}>
            {item.name_file}
          </Link>
        ) : (
          <a
            onClick={(e) => downloadFile(e, item)}
            href={"file-attachments/" + JSON.stringify(item.id)}
          >
            {item.name_file}
          </a>
        ),
    },
    {
      title: "Description",
      dataIndex: "description",
      key: "description",
    },
  ];

  const downloadFile = async (e, item) => {
    e.preventDefault();
    let id = item.id;
    await CApi({
      method: "GET",
      url: "api/detail-file-attachment/" + id,
    })
      .then((res) => {
        toBlob(res.data, res);
      })
      .catch((_err) => {});
  };

  const toBlob = async (item) => {
    await fetch(item.type + item.file)
      .then((res) => res.blob())
      .then((res) => {
        var blobUrl = URL.createObjectURL(res);
        var a = document.getElementById("blob");
        a.href = blobUrl;
        a.download = item.name_file;
        a.click();
        a.href = "";
      });
  };

  const columns = [
    {
      title: "Name",
      key: "name",
      dataIndex: "name",
      width: 250,
    },
    {
      title: "File Name",
      width: 450,
      key: "name_file",
      render: (item) =>
        item.mime == "application/pdf" ? (
          <Link
            href={"file-attachments/" + JSON.stringify(item.id)}
            style={{ color: "#0000FF" }}
          >
            {item.name_file}
          </Link>
        ) : (
          <a
            onClick={(e) => downloadFile(e, item)}
            href={"file-attachments/" + JSON.stringify(item.id)}
            style={{ color: "#0000FF" }}
          >
            {item.name_file}
          </a>
        ),
    },
    {
      title: "Description",
      dataIndex: "description",
      key: "description",
    },
  ];

  // Table
  const columnsAudit = [
    {
      title: "Barcode",
      dataIndex: "tag_number",
      key: "tag_number",
      width: 100,
      align: "center",
      ellipsis: true,
    },
    {
      title: "No Induk Barang",
      dataIndex: "no_induk_barang",
      width: 150,
      key: "no_induk_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "AMS NO",
      dataIndex: "no_barang",
      width: 100,
      key: "no_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "REINS NO",
      dataIndex: "old_number",
      width: 100,
      key: "old_number",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Name",
      key: "name",
      width: 400,
      align: "center",
      dataIndex: "name",
      ellipsis: true,
    },
    {
      title: "Branch",
      key: "branch",
      align: "center",
      dataIndex: "branch",
      ellipsis: true,
    },
    {
      title: "Departement",
      align: "center",
      width: 200,
      key: "vendor",
      dataIndex: "vendor",
      ellipsis: true,
    },
    {
      title: "Ownership",
      key: "ownership",
      align: "center",
      dataIndex: "ownership",
      ellipsis: true,
    },
    {
      title: "Code Activa",
      key: "code_activa",
      align: "center",
      width: 200,
      dataIndex: "code_activa",
      ellipsis: true,
    },
    {
      title: "Location",
      align: "center",
      width: 200,
      key: "location",
      dataIndex: "location",
      ellipsis: true,
    },
    {
      title: "Tanggal Perolehan",
      dataIndex: "tgl_perolehan",
      key: "tgl_perolehan",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Jenis Barang",
      key: "asset_type",
      align: "center",
      dataIndex: "asset_type",
      ellipsis: true,
    },
    {
      title: "Payment Voucher",
      key: "payment_voucher",
      align: "center",
      dataIndex: "payment_voucher",
      ellipsis: true,
    },
    {
      title: "Merek Barang",
      key: "merek_barang",
      align: "center",
      dataIndex: "merek_barang",
      ellipsis: true,
    },
    {
      title: "Model",
      key: "model",
      align: "center",
      dataIndex: "model",
      ellipsis: true,
    },
    {
      title: "Harga Perolehan",
      key: "harga_perolehan",
      align: "center",
      dataIndex: "harga_perolehan",
      ellipsis: true,
      render: (item, record) => {
        return record.currency_perolehan == "IDR"
          ? new Intl.NumberFormat("id-ID", {
              style: "currency",
              currency: "IDR",
            }).format(item)
          : new Intl.NumberFormat("en-US", {
              style: "currency",
              currency: "USD",
            }).format(item);
      },
    },
    {
      title: "Currency Perolehan",
      dataIndex: "currency_perolehan",
      key: "currency_perolehan",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Serial Number",
      key: "serial_number",
      align: "center",
      dataIndex: "serial_number",
      ellipsis: true,
    },
    {
      title: "Anggaran Asset",
      key: "anggaran",
      align: "center",
      dataIndex: "anggaran",
      ellipsis: true,
    },
    {
      title: "Masa Manfaat (Year)",
      dataIndex: "masa_manfaat",
      key: "masa_manfaat",
      align: "center",
      ellipsis: true,
    },

    {
      title: "Flag",
      dataIndex: "flag",
      key: "flag",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Status Activa",
      key: "status_activa",
      dataIndex: "status_activa",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Updated By",
      dataIndex: "updated_by",
      key: "updated_by",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Kondisi Barang",
      key: "status",
      dataIndex: "status",
      fixed: "right",
      align: "center",
      width: 150,
      ellipsis: true,
      render: (text, record) => {
        return {
          props: {
            style: {
              background: record.before ? "#fc8282" : "#afdc82",
            },
          },
          children: text,
        };
      },
    },
    {
      title: "Create Date",
      dataIndex: "date",
      key: "date",
      fixed: "right",
      width: 100,
      ellipsis: true,
      render: (text, record) => {
        return {
          props: {
            style: {
              background: record.before ? "#fc8282" : "#afdc82",
            },
          },
          children: text,
        };
      },
    },
  ];
  //Akhir Table

  return (
    <Row>
      <CTitle secondTitle=" Information" />
      <Col span={24}>
        <Card
          cover={
            <Col
              span={24}
              style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
            />
          }
        >
          <Row>
            <Col span={22}>
              <CPageTitle title={name} />
            </Col>
            <Col span={2}>
              <Button
                type="primary"
                onClick={() => router.push("/asset")}
                style={{ float: "right" }}
              >
                Back
              </Button>
            </Col>
          </Row>
          <Row gutter={4} style={{ flex: 1 }}>
            <Tabs style={{ width: "100%" }} defaultActiveKey="1">
              <TabPane tab="Data" key="1">
                <CDetailAssetData
                  noInduk={noInduk}
                  tagNumber={tagNumber}
                  noBarang={noBarang}
                  oldNumber={oldNumber}
                  name={name}
                  branch={branch}
                  location={location}
                  assetCategory={assetCategory}
                  flag={flag}
                  status={status}
                  uselife={uselife}
                  remark={remark}
                  model={model}
                  masaManfaat={masaManfaat}
                  serialNumber={serialNumber}
                  partNumber={partNumber}
                  anggaran={anggaran}
                  statusActiva={statusActiva}
                  value={value}
                  year={year}
                  vendor={vendor}
                  qrCodeUrl={qrCodeUrl}
                  codeActiva={codeActiva}
                  ownership={ownership}
                  assetType={assetType}
                  merk={merk}
                  tglPerolehan={tglPerolehan}
                  currencyPerolehan={currencyPerolehan}
                  hargaPerolehan={hargaPerolehan}
                  paymentVoucher={paymentVoucher}
                  isDisposal={isDisposal}
                  disposalDescription={disposalDescription}
                />
              </TabPane>
              <TabPane tab="Photos" key="2">
                <Row>
                  <CDetailAssetImage
                    image={isImageSatu}
                    imageUrl={imageUrlSatu}
                    isImageDua={isImageDua}
                    imageUrlDua={imageUrlDua}
                  />
                </Row>
              </TabPane>
              <TabPane tab="Attachments" key="3">
                <CDetailAttachments
                  columns={columns}
                  columnsDisposal={columnsDisposal}
                  loading={loadingAttachments}
                  loadingDisposal={loadingDisposal}
                  data={attachments}
                  dataDisposal={disposal}
                />
              </TabPane>
              <TabPane tab="History" key="4">
                <Row>
                  <Row style={{ float: "right" }}>
                    <Col span={24}>
                      <h4 style={{ marginBottom: "14px" }}>
                        <Text
                          style={{
                            fontSize: 16,
                            color: "rgba(250, 5, 5, 0.5)",
                            fontWeight: "bold",
                          }}
                        >
                          &#9632;
                        </Text>
                        <Text>Before</Text>
                        {"  "}
                        <Text
                          style={{
                            fontSize: 16,
                            color: "rgba(95, 185, 5, 0.5)",
                            fontWeight: "bold",
                          }}
                        >
                          &#9632;
                        </Text>
                        <Text>After</Text>
                      </h4>
                    </Col>
                  </Row>
                  <Table
                    columns={columnsAudit}
                    dataSource={dataAudit}
                    scroll={{ x: 4000, y: 500 }}
                    size="small"
                    rowKey="id"
                    pagination={false}
                    loading={loadingHistory}
                    rowClassName={(record, index) =>
                      record.before ? "bg-red" : "bg-green"
                    }
                    bordered
                  />
                </Row>
              </TabPane>
            </Tabs>
          </Row>
        </Card>
      </Col>
      <a id="blob" style={{ display: "none" }} />
    </Row>
  );
};

export default AssetInformation;
